@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sidebar {
  margin-top: 5px;
  display: grid;
  grid-template-rows: 1fr 3fr;
  grid-row-gap: 2px;
  /* padding: 20px; */
  background-color: #66c8d4;
}

.status-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px;
}

hr {
  height: 5px;
  width: 100%;
  background-color: #171c28;
  border: none;
}
.btn {
  /* width: 100%; */
  background-color: #66c8d4 !important;
  border-radius: 5px;
  color: #fff;
}

.clock-in {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 0px;
  margin-bottom: 10px;
}

.clock-in label {
  font-size: 30px;
  color: #ce4ad8;
}

.clock-in button {
  border-radius: 50%;
  /* padding: 5px; */
  width: 50px;
  height: 50px;
  background: #ce4ad8;
  border: none;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.clock-in button:hover {
  cursor: pointer;
  background-color: #66c8d4;
}

.clock-in-button {
  text-align: center;
}

.status-container i {
  font-size: 30px;
  padding: 5px;
  color: #292d39;
}

.social-content {
  background-color: #292d39;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
  color: #fff;
}

.status {
  background-color: #292d39;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
  color: #fff;
}

.status p:nth-child(2) {
  margin-bottom: 5px;
  align-self: left;
}

.side-nav-open {
  position: absolute !important;
}

.nav-custom {
  /* margin: 0; */
  background-color: #292d39 !important;
  border-radius: 0px 0 5px 0px;
}

.nav-custom-wrapper {
  padding: 0 20px;
}

.nav-links {
  font-size: 20px;
}

.link-1:hover {
  background-color: #ce4ad8;
}
.link-2:hover {
  background-color: #d46b35;
}
.link-3:hover {
  background-color: #3fb485;
}
.link-4:hover {
  background-color: #66c8d4;
}

/* li:hover {
  background-color: #39b6ff;
} */

.loged-in-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #292d39;
  padding: 0 10px;
  border-radius: 0px 0 0px 5px;
  box-shadow: 2px 0px 10px 0 rgba(0, 0, 0, 0.14);
}

.loged-in-nav .btn {
  margin: 0 10px;
  border-radius: 5px;
  /* background-image: linear-gradient(to right, #66c8d4, #39b6ff) !important; */
  background-color: #39b6ff;
}

.signup-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40%;
}

input {
  width: 100%;
}
.full-name {
  display: flex;
}

.full-name {
  display: flex;
  /* justify-content: space-between; */
}

.dashboard {
  min-height: 100vh;
  background-color: #181c28;
  color: #7882a2;
  /* display: grid;
  grid-template-columns: 1fr 4fr; */
  padding: 30px;
}

.top-dash-contents {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 50px;
}
.project-details-view {
  padding: 20px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .top-dash-contents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .top-dash-contents:first-child {
    width: 100%;
  }
}

.task-container {
  background-color: #282d39;
  border-radius: 5px;
  min-height: 500px !important;
  width: 50% !important;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px -1px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  margin: 20px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .task-container {
    width: 100% !important;
  }
}

.project-single-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  border-bottom: 2px solid #66c7d470;
  color: #fff;
}

button {
  background: transparent;
  border: none;
  color: #fff !important;
}

button.select-project {
  cursor: pointer;
  color: #7882a2;
}

.project-single-row i {
  font-size: 20px;
  color: #66c8d4;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.project-single-row i:hover {
  font-size: 20px;
  cursor: pointer;
  color: #ce4ad8;
}

.project-container {
  background-color: #282d39;
  grid-area: project;
  border-radius: 5px;
  min-height: 500px !important;
  width: 50% !important;
  margin: 20px;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px -1px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #66c7d470;
  padding: 10px;
  color: #fff;
}

.project-header i {
  color: #66c8d4;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.project-header i:hover {
  color: #d46b35;
}

.project-header button {
  background: transparent;
  border: none;
}
.project-header button:hover {
  cursor: pointer;
}
.project-header h5 {
  margin: 0;
}

.modal-content {
  background-color: #282d39;
  color: #fff !important;
}

.modal {
  border-radius: 5px !important;
  background: none;
}
.modal-footer {
  background-color: #282d39 !important;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .project-container {
    width: 100% !important;
  }
}

.edit-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.edit-header i {
  font-size: 25px;
  margin-left: 10px;
  color: red;
}

.edit-header button {
  display: flex;
  align-items: center;
  color: red;
  border: 2px solid red;
  border-radius: 5px;
  padding: 5px;
}

.edit-header button:hover {
  cursor: pointer;
}

.project-details-content {
  background-color: #282d39;
  border-radius: 5px;
  padding: 20px;
  min-height: 500px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.exit-details button {
  background: transparent;
  border: none;
}

.exit-details i {
  font-size: 30px;
  color: #ce4ad8;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.exit-details i:hover {
  cursor: pointer;
  color: #39b6ff;
}

.exit-details {
  align-self: right !important;
}

.edit-modal {
  position: absolute;
}

.project-details {
  padding: 20px;
}

.calender-container {
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px -1px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  padding: 20px;
  background-color: #282d39;
  border-radius: 5px;
  color: #fff;
}

.fc-view > table {
  height: 500px !important;
}

.fc th,
.fc td {
  height: 47px;
  border-width: 1px !important;
}

.fc-unthemed th,
.fc-unthemed td,
.fc-unthemed thead,
.fc-unthemed tbody,
.fc-unthemed .fc-divider,
.fc-unthemed .fc-row,
.fc-unthemed .fc-content,
.fc-unthemed .fc-popover,
.fc-unthemed .fc-list-view,
.fc-unthemed .fc-list-heading td {
  border-color: #66c8d4 !important;
}

.fc-unthemed td.fc-today {
  background: #66c7d444 !important;
}

.fc-day-grid-container {
  height: 500px !important;
}

.user-account-content {
  min-height: 100vh;
  background-color: #282d39;
  border-radius: 5px;
  margin: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px -1px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  color: #fff;
}

.user-account-header {
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px -1px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding: 10px;
  width: 60%;
  min-height: 200px;
  margin: 20px;
  border: 2px solid #66c8d4;
  position: relative;
}

.user-account-header {
  display: flex;
  justify-content: space-evenly;
}

.edit-btn {
  position: absolute;
  font-size: 20px;
  right: 3%;
  top: 3%;
}

body {
  font-family: "Lato", sans-serif;

  background-color: #181c28;
}

.sign-up {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 20px;
  min-height: 100vh;
}

.side-nav-btn {
  position: absolute !important;
  top: 50%;
  height: 50px;
  padding: 0;
  border-radius: 0 5px 5px 0;
}

.side-nav-btn > i {
  padding: 5px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {
  .btn:nth-child(2) {
    display: none;
  }
}

